.modal{
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.7);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
   display: none;
    pointer-events: none;
    transition: 0.5s;
    z-index: 1;

}
.modal.active{
    z-index: 3;
    display: flex;
    pointer-events: all;
}

.modal__content {
    padding: 20px;
    border-radius: 12px;
    background-color: rgba(82, 34, 155, 1);
    width: 50vw;
    transform: scale(0.5);
    transition: 0.4s all;
    z-index: 1;
}

.modal__content.active {
transform: scale(1);
border-color: rgb(0, 143, 143);
}