        }
        .progress-bar-main-per-title{
            position: relative;
            display: block;
            height: 100%;
            width: 95%;
            border-radius:6px;
            background: rgba(199, 20, 148, 1);
            animation: progress 0.4s ease-in-out forwards;
            gap: 5px;
        }
       @keyframes progress {
        0% {
            width: 0;
            opacity: 1;
        }
        100%{
            opacity: 1;
        }
       }
       .progress-bar-tooltip{
        right: -14px;
        top: -28px;
        font-size: 15px;
        font-weight: bold;
        color: var(--pink);
            padding: 2px 6px;
        border-radius: 3px;
        z-index: 1;
       }
     
    }
}